<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
import { formatDate } from "../scripts/Download";
export default {
  components: { DatePicker },
  data() {
    return {
      picked: "ThreeDays",
      time: "",
    };
  },
  methods: {
    Refresh() {
      const i = {
        interval: this.picked,
        time: this.time,
      };
      this.$emit("refresh", i);
    },
    Disabled(date) {
      const today = new Date();
      return date > today;
    },
  },
  mounted() {
    const date = new Date();
    this.time = formatDate(date);
  },
};
</script>

