/* 99 –î–∞–≤–ª–µ–Ω–∏–µ, 100 –í–ª–∞–∂–Ω–æ—Å—Ç—å, 101 –ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞, 102 –°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞, 103 –¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞, 369 –ü–æ—Ä—ã–≤—ã */
/* –ö–æ–¥—ã –¥–∞–Ω–Ω—ã—Ö */
const indicators = '99,100,101,102,103,369';
const indicatorWindDirection = '101';

/* –°–æ–∑–¥–∞–Ω–∏–µ –¥–∞—Ç */
function CreateDates(queryData, settings) {
  let year = "";
  let month = "";
  let day = "";
  if (settings.time) {
    year = settings.time.split("-")[0];
    month = settings.time.split("-")[1] - 1;
    day = settings.time.split("-")[2];
  }
  if (queryData.date) {
    year = queryData.date.split("-")[0];
    month = queryData.date.split("-")[1] - 1;
    day = queryData.date.split("-")[2];
    if (queryData.hour == undefined) { queryData.hour = "00-00" }
  }

  const interval = Invtervals(settings.interval);
  const date = new Date();
  const hour = queryData.date ? queryData.hour.split("-")[0] : formateHour(date.getHours());
  const dateBeginArchive = queryData.date || settings.time ? new Date(year, month, day, hour, 0, 0) : new Date();
  const dateEndArchive = queryData.date || settings.time ? new Date(year, month, day, hour, 0, 0) : new Date();
  const dateBeginForecast = queryData.date ? new Date(year, month, day, hour, 0, 0) : new Date();
  const dateEndForecast = queryData.date ? new Date(year, month, day, hour, 0, 0) : new Date();

  dateBeginArchive.setDate(dateBeginArchive.getDate() - interval.days);
  dateEndForecast.setDate(dateEndForecast.getDate() + 3);

  const hourForecast = formateHour(dateBeginForecast.getHours());
  const timeEnd = formatDate(dateEndArchive);
  const timeEndForecast = formatDate(dateEndForecast);
  const timeBegin = formatDate(dateBeginArchive);
  const timeBeginForecast = formatDate(dateBeginForecast);
  return { timeEnd, timeEndForecast, timeBegin, timeBeginForecast, hour, hourForecast, interval: interval.url, intervalValue: interval.value }
}
/*  */
function Invtervals(value) {
  if (value == "ThreeDays") { return { url: "1hour", days: 3, value: value } }
  if (value == "Month") { return { url: "12hour", days: 30, value: value } }
  if (value == "HalfYear") { return { url: "1day", days: 90, value: value } }
}

/* –ü–æ–ª—É—á–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –∑–∞ —Ç—Ä–æ–µ —Å—É—Ç–æ–∫ */
