/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö */
async function XML2JSON(e, interval) {
  const temperature = { time: [], values: [], name: "–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ (¬∞C)", type: "temperature" };
  const pressure = { time: [], values: [], name: "–î–∞–≤–ª–µ–Ω–∏–µ (–º–º —Ä—Ç. —Å—Ç)", type: "pressure" };
  const humidity = { time: [], values: [], name: "–í–ª–∞–∂–Ω–æ—Å—Ç—å (%)", type: "humidity" };
  const winddirect = { time: [], values: [], name: "–ü—Ä–µ–æ–±–ª–∞–¥–∞—é—â–µ–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞", type: "winddirect" };
  const winddirectcard = { time: [], values: [], name: "–ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤–µ—Ç—Ä–∞ (–ö–∞—Ä—Ç–æ—á–∫–∞)", type: "winddirectcard" };
  const windspeed = { time: [], values: { s: [], r: [] }, name: "–°–∫–æ—Ä–æ—Å—Ç—å –≤–µ—Ç—Ä–∞ (–º/—Å)", type: "windspeed" };
  const intervals = { three: ["01:00", "13:00", "19:00"], month: ["03", "06", "09", "12", "15", "18", "21", "24", "27", "30"], halfyear: ["02", "10", "18", "26"] }
  const r = e.text().then(e => {
    const dom = new DOMParser();
    const xml = dom.parseFromString(e, "text/xml");
    const aggvalues = xml.getElementsByTagName("aggvalue");
    let dayBegin = "";
    let count = 0;
    aggvalues.forEach((element) => {
      const indicator = element.getAttribute("indicator")
      const time = element.getAttribute("time").substring(0, element.getAttribute("time").length - 3);
      const value = Number(element.getElementsByTagName("avg")[0].innerHTML).toFixed(1);
      switch (indicator) {
        case '99':
          pressure.time.push(
            time
          );
          pressure.values.push(
            value
          )
          break;
        case '100':
          humidity.time.push(
            time
          );
          humidity.values.push(
            value
          );
          break;
        case '101':
          if (time.split(' ')[0] != dayBegin) {
            dayBegin = time.split(' ')[0]
            winddirect.values.push([]);
            winddirect.time.push(
              time.split(" ")[0]
            );
            count++;
          }
          winddirect.values[count - 1].push(
            DirectionRound(value)
          );
          if (interval == "ThreeDays") {
            for (let i = 0; i < intervals.three.length; i++) {
              if (time.split(" ")[1] == intervals.three[i]) {
                winddirectcard.time.push(time)
                winddirectcard.values.push(Number(DirectionRound(value)))
              }
            }
          }
          if (interval == "Month") {
            for (let i = 0; i < intervals.month.length; i++) {
              if (time.split(" ")[0].split("-")[2] == intervals.month[i] && time.split(" ")[1] == "00:00") {
                winddirectcard.time.push(time)
                winddirectcard.values.push(Number(DirectionRound(value)))
              }
            }
          }
          if (interval == "HalfYear") {
            for (let i = 0; i < intervals.halfyear.length; i++) {
              if (time.split(" ")[0].split("-")[2] == intervals.halfyear[i]) {
                winddirectcard.time.push(time)
                winddirectcard.values.push(Number(DirectionRound(value)))
              }
            }
          }
          break;
        case '102':
          windspeed.time.push(
            time
          );
          windspeed.values.s.push(
            value
          );
          break;
        case '103':
          temperature.time.push(
            time
          );
          temperature.values.push(
            value
          );
          break;
        case '369':
          windspeed.values.r.push(
            value
          );
          break;
        default:
          console.log("–ù–µ—Ç —Ç–∞–∫–∏—Ö –∑–Ω–∞—á–µ–Ω–∏–π");
      }
    })
    return [
      temperature, pressure, humidity, windspeed, winddirectcard, winddirect
    ]
  })
  return r;
}

/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–æ–≤—ã—Ö –¥–∞–Ω–Ω—ã—Ö */
async function XML2TextForecast(e) {
  const ugms = [];
  const russia = [];
  const r = e.text().then(e => {
    const dom = new DOMParser();
    const xml = dom.parseFromString(e, "text/xml");
    const values = xml.getElementsByTagName("value");
    values.forEach((element) => {
      const time = element.getAttribute("time")
      const value = element.innerHTML
      if (time.split(' ')[1] == "12:01:00") {
        ugms.push({
          time: time.split(' ')[0],
          value: value
        })
      }
      if (time.split(' ')[1] == "12:02:00") {
        russia.push(
          {
            time: time.split(' ')[0],
            value: value
          }
        );
      }
    })
    return { ugms, russia }
  })
  return r;
}

/* –ü—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç–∞–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –Ω–∞ –≥—Ä–∞—Ñ–∏–∫–µ */
