<template>
  <div class="container-fluid">
    <div class="container-fluid style-of-institute">
      <div class="row">
        <div class="col">
          <h5 class="header bold mt-3">
            –ê—Ä—Ö–∏–≤ –¥–∞–Ω–Ω—ã—Ö –º–µ—Ç–µ–æ—Å—Ç–∞–Ω—Ü–∏–∏ –ò–í–ú –°–û –†–ê–ù –∑–∞ {{ text }}
          </h5>
        </div>
        <div class="col-auto"><Settings @refresh="LoadComponent" /></div>
        <div class="col-2 p-2">
          <img
            src="../assets/rolling.svg"
            style="widht: 34px; height: 34px; padding: 1 rem"
            v-if="doneLoading == false"
          />
        </div>
      </div>
    </div>
    <div class="container-fluid mt-3">
      <div class="row">
        <div class="col-xl-6">
          <div
            class="row"
            v-for="(item, index) in dataForChart.slice(0, 3)"
            :key="index"
          >
            <div class="col">
              <h5 class="bold text-center">{{ item.name }}</h5>
              <BarChart
                ref="testT"
                v-if="item.type == 'temperature'"
                :data="item"
                :interval="interval"
                style="height: 350px"
                @done="LoadingComplete"
              />
              <BarChart
                ref="testT"
                :interval="interval"
                v-else
                :data="item"
                style="height: 200px"
              />
            </div>
          </div>
        </div>
        <div class="col-xl-6 row-flex">
          <div
            class="row"
            v-for="(item, index) in dataForChart.slice(3)"
            :key="index"
          >
            <div class="col">
              <h5 class="bold text-center" v-if="item.type != 'winddirectcard'">
                {{ item.name }}
              </h5>
              <CardWind
                :data="item"
                v-if="item.type == 'winddirectcard'"
                class="displayCard mb-5"
              />
              <BarChart
                :data="item"
                :interval="interval"
                :test="test"
                :alldata="dataForChart"
                style="height: 350px"
                @hover="Hover"
                v-if="item.type == 'windspeed'"
              />
              <div
                class="row justify-content-center"
                v-if="item.type == 'winddirect'"
              >
                <div
                  class="col-sm-auto col-md-4 col-lg-3 col-xl-3 col-8"
                  v-for="(radarData, index) in item.values"
                  :key="index"
                >
                  <h6
                    class="text-center p-0 mt-3"
                    v-if="item.time[index].length > 1"
                  >
                    {{ item.time[index] }}
                  </h6>
                  <h6 class="text-center p-0 mt-3" v-else>
                    {{ item.time[index] }}
                  </h6>
                  <RadarChart
                    class="cardWindTest"
                    :data="{
                      time: item.time,
                      values: radarData,
                      type: item.type,
                      forecast: false,
                    }"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Settings from "./Settings.vue";
import BarChart from "./BarChart";
import RadarChart from "./RadarChart";
import CardWind from "./CardWind";
import { DownloadInformationNow } from "../scripts/Download";
import { DownloadInfomationWindDirection } from "../scripts/Download";
import { WindLayout } from "../scripts/Download";
import { CreateDates } from "../scripts/Download";
import { Style } from "../scripts/Download";
export default {
  data() {
    return {
      media: false,
      interval: "ThreeDays",
      doneLoading: true,
      test: "",
      dn: "",
      dataForChart: "",
    };
  },
  components: {
    BarChart,
    RadarChart,
    CardWind,
    Settings,
  },
  computed: {
    text: function () {
      let t = "";
      if (this.interval == "ThreeDays") {
        t = "—Ç—Ä–∏ –¥–Ω—è";
      }
      if (this.interval == "Month") {
        t = "–º–µ—Å—è—Ü";
      }
      if (this.interval == "HalfYear") {
        t = "–∫–≤–∞—Ä—Ç–∞–ª";
      }
      return t;
    },
  },
  methods: {
    LoadComponent(settings) {
      if (!settings) {
        settings = {
          interval: "ThreeDays",
        };
      }
      this.interval = settings.interval;
      const queryData = this.$route.query;
      const dates = CreateDates(queryData, settings);
      DownloadInformationNow(dates).then((e) => {
        DownloadInfomationWindDirection(dates).then((w) => {
          if (this.interval != "ThreeDays") {
            e[5] = WindLayout(w[5], this.interval);
          } else {
            e[5] = w[5];
          }
          for (let i = 0; i < 4; i++) {
            e[i] = Style(e[i]);
          }
          this.dataForChart = e;
        });
      });
    },
    Refresh(i) {
      this.doneLoading = false;
      this.interval = i.interval;
      this.$emit("refresh", i);
    },
    LoadingComplete() {
      this.doneLoading = true;
    },
    Hover(index) {
      let component = this.$refs.testT;
      component.forEach((c) => {
        let chart = c._data._chart;
        const initial–°olor = chart.data.datasets[0].backgroundColor[index];
        chart.data.datasets[0].backgroundColor[index] = "red";
        chart.update();
        chart.data.datasets[0].backgroundColor[index] = initial–°olor;
      });
    },
  },
  async mounted() {
    this.LoadComponent();
  },
};
</script>

<style>
.row-flex {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
</style>